import React, { Fragment, useState, useEffect } from "react";
import { Link } from "react-router-dom";

const Header = () => {
  const [headerSticky, setHeaderSticky] = useState(false);
  useEffect(() => {
    const handleScroll = () => {
      const windowHeight = window.innerHeight;
      const scrollPosition = window.pageYOffset;
      if (scrollPosition > windowHeight * 0.3) {
        setHeaderSticky(true);
      } else {
        setHeaderSticky(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const closeNavbar = () => {
    const navbarToggler = document.querySelector(".navbar-toggler");
    if (navbarToggler && navbarToggler.classList.contains("collapsed")) {
      return;
    }

    const headerNav = document.getElementById("header-nav");
    if (headerNav && headerNav.classList.contains("show")) {
      navbarToggler.click();
    }
  };

  return (
    <Fragment>
      <header id="header" className="sticky-top">
        <nav
          className={`primary-menu navbar navbar-expand-none navbar-overlay navbar-overlay-dark bg-transparent border-bottom-0 text-5 fw-600 ${
            headerSticky ? "sticky-on-top" : ""
          }`}
        >
          <div className="container">
            <Link className="logo text-decoration-none" to="/" title="Callum">
              {" "}
              <span className="fw-bold text-dark">PRAVIN-DEV</span>
            </Link>

            <div className="text-4 ms-auto me-2">
              <span className="text-4 me-2">
                <i className="fas fa-phone"></i>
              </span>
              (+91) 978 974 1475
            </div>
            <button
              className="navbar-toggler collapsed"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#header-nav"
              aria-expanded="false"
            >
              <span></span>
              <span></span>
              <span></span>
            </button>
            <div id="header-nav" className="collapse navbar-collapse">
              <div className="d-flex flex-column h-100 align-items-center justify-content-center">
                <ul className="navbar-nav">
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll active"
                      href="#home"
                      onClick={closeNavbar}
                    >
                      Home
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#about"
                      onClick={closeNavbar}
                    >
                      About Me
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#services"
                      onClick={closeNavbar}
                    >
                      What I Do
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#resume"
                      onClick={closeNavbar}
                    >
                      Resume
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#portfolio"
                      onClick={closeNavbar}
                    >
                      Portfolio
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#faq"
                      onClick={closeNavbar}
                    >
                      FAQ
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#testimonial"
                      onClick={closeNavbar}
                    >
                      Client Speak
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link smooth-scroll"
                      href="#contact"
                      onClick={closeNavbar}
                    >
                      Contact Me
                    </a>
                  </li>
                </ul>
                <ul className="social-icons social-icons-light social-icons-lg social-icons-light d-inline-flex mt-4">
                  <li className="social-icons-twitter">
                    <Link
                      data-bs-toggle="tooltip"
                      to="https://twitter.com/PravinDev10"
                      target="_blank"
                      title=""
                      data-bs-original-title="Twitter"
                    >
                      <i className="fab fa-twitter"></i>
                    </Link>
                  </li>
                  <li className="social-icons-facebook">
                    <Link
                      data-bs-toggle="tooltip"
                      to="https://wa.me/+919789741475"
                      target="_blank"
                      title=""
                      data-bs-original-title="Facebook"
                    >
                      <i className="fab fa-whatsapp"></i>
                    </Link>
                  </li>
                  <li className="social-icons-instagram">
                    <Link
                      data-bs-toggle="tooltip"
                      to="http://www.instagram.com/"
                      target="_blank"
                      title=""
                      data-bs-original-title="Instagram"
                    >
                      <i className="fab fa-instagram"></i>
                    </Link>
                  </li>
                  <li className="social-icons-linkedin">
                    <Link
                      data-bs-toggle="tooltip"
                      to="https://www.linkedin.com/in/pravin-raj-dev/"
                      target="_blank"
                      title=""
                      data-bs-original-title="Linkedin"
                    >
                      <i className="fab fa-linkedin-in"></i>
                    </Link>
                  </li>
                  <li className="social-icons-dribbble">
                    <Link
                      data-bs-toggle="tooltip"
                      to="mailto:pravinpersonal3@gmail.com"
                      target="_blank"
                      title=""
                      data-bs-original-title="Dribbble"
                    >
                      <i className="fas fa-envelope"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </nav>
      </header>
    </Fragment>
  );
};

export default Header;
