import React, { Fragment } from "react";

const Aboutus = () => {
  return (
    <Fragment>
      <section id="about" className="section">
        <div className="container">
          <p
            className="text-center mb-2 wow animate__animated animate__fadeInUp"
            style={{ visibility: "visible" }}
          >
            <span className="bg-primary text-dark px-2">About Me</span>
          </p>
          <h2
            className="text-10 fw-600 text-center mb-5 wow animate__animated animate__fadeInUp"
            style={{ visibility: "visible" }}
          >
            Know Me More
          </h2>

          <div className="row">
            <div
              className="col-lg-8 text-center text-lg-start wow animate__animated animate__fadeInUp"
              style={{ visibility: "visible" }}
            >
              <h2 className="text-8 fw-400 mb-3">
                Hi, I'm{" "}
                <span className="fw-700 border-bottom border-3 border-primary">
                  Pravin
                </span>
              </h2>
              <p className="text-5">
                With over 10 years of experience as a web developer and
                consultant, I am dedicated to crafting user-centric web
                experiences. I champion clean, intuitive UIs and efficient
                development processes, ensuring websites deliver tangible value
                to users. Through collaborative partnerships, I have
                consistently helped clients achieve exceptional results. My
                leadership skills foster a strong team environment, guaranteeing
                on-time, on-budget project delivery that surpasses expectations.
              </p>
            </div>
            <div
              className="col-lg-4 mt-4 mt-lg-0 wow animate__animated animate__fadeInUp"
              data-wow-delay="0.2s"
              style={{ visibility: "visible", animationDelay: "0.2s" }}
            >
              <div className="featured-box style-4">
                <div className="featured-box-icon text-25 fw-500 bg-primary rounded-circle">
                  <span
                    className="wow animate__animated animate__heartBeat"
                    data-wow-delay="1.3s"
                    style={{ visibility: "visible", animationDelay: "1.3s" }}
                  >
                    10+
                  </span>
                </div>
                <h3
                  className="text-7 wow animate__animated animate__rubberBand"
                  data-wow-delay="2s"
                  style={{ visibility: "visible", animationDelay: "2s" }}
                >
                  Year of <span className="fw-700">Experiance</span>
                </h3>
              </div>
            </div>
          </div>
          <div className="row gy-3 mt-4">
            <div
              className="col-12 col-sm-6 col-lg-4 wow animate__animated animate__fadeInUp"
              style={{ visibility: "visible" }}
            >
              <p className="text-muted fw-500 mb-0">Name:</p>
              <p className="text-4 text-dark fw-600 mb-0">Pravin</p>
            </div>
            <div
              className="col-12 col-sm-6 col-lg-4 wow animate__animated animate__fadeInUp"
              data-wow-delay="0.2s"
              style={{ visibility: "visible", animationDelay: "0.2s" }}
            >
              <p className="text-muted fw-500 mb-0">Email:</p>
              <p className="text-4 fw-600 mb-0">
                <a
                  className="link-dark"
                  targe="__blank"
                  href="mailto:pravinpersonal3@gmail.com"
                >
                  pravinpersonal3@gmail.com
                </a>
              </p>
            </div>

            <div
              className="col-12 col-sm-6 col-lg-4 wow animate__animated animate__fadeInUp"
              data-wow-delay="0.4s"
              style={{ visibility: "visible", animationDelay: "0.4s" }}
            >
              <p className="text-muted fw-500 mb-0">From:</p>
              <p className="text-4 text-dark fw-600 mb-0">
                {" "}
                Tamil Nadu, India.
              </p>
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default Aboutus;
