import React, { Fragment } from "react";
import { Link } from "react-router-dom";

const Form = () => {
  return (
    <Fragment>
      <section id="contact" className="section bg-primary">
        <div className="container">
          <div className="row">
            <div
              className="col-lg-5 text-center text-lg-start wow fadeInUp"
              style={{ visibility: "visible" }}
            >
              <h2 className="text-10 fw-600 mb-5">Let's get in touch</h2>
              <p className="text-5 mb-5">
                Feel free to open up about your projects and development
                hurdles. The more details, the better our initial discussion!
              </p>

              <h3 className="text-5 fw-600">Call:</h3>
              <p className="text-4">(+91) 978 974 1475</p>
              <ul className="social-icons social-icons-lg justify-content-center justify-content-lg-start mt-5">
                <li className="social-icons-twitter">
                  <Link
                    data-bs-toggle="tooltip"
                    to="https://twitter.com/PravinDev10"
                    target="_blank"
                    title=""
                    data-bs-original-title="Twitter"
                  >
                    <i className="fab fa-twitter"></i>
                  </Link>
                </li>
                <li className="social-icons-facebook">
                  <Link
                    data-bs-toggle="tooltip"
                    to="https://wa.me/+919789741475"
                    target="_blank"
                    title=""
                    data-bs-original-title="Facebook"
                  >
                    <i className="fab fa-whatsapp"></i>
                  </Link>
                </li>
                <li className="social-icons-instagram">
                  <Link
                    data-bs-toggle="tooltip"
                    href="http://www.instagram.com/"
                    target="_blank"
                    title=""
                    data-bs-original-title="Instagram"
                  >
                    <i className="fab fa-instagram"></i>
                  </Link>
                </li>
                <li className="social-icons-github">
                  <Link
                    data-bs-toggle="tooltip"
                    to="https://github.com/pravindev12"
                    target="_blank"
                    title=""
                    data-bs-original-title="GitHub"
                  >
                    <i className="fab fa-github"></i>
                  </Link>
                </li>
                <li className="social-icons-dribbble">
                  <Link
                    data-bs-toggle="tooltip"
                    to="mailto:pravinpersonal3@gmail.com"
                    target="_blank"
                    title=""
                    data-bs-original-title="Dribbble"
                  >
                    <i className="fas fa-envelope"></i>
                  </Link>
                </li>
              </ul>
            </div>
            <div
              className="col-lg-6 ms-auto mt-5 mt-lg-0 wow fadeInUp"
              data-wow-delay="0.3s"
              style={{ visibility: "visible", animationDelay: "0.3s" }}
            >
              <h2 className="text-10 fw-600 text-center text-lg-start mb-5">
                Estimate your Project?
              </h2>

              <form
                id="contact-form"
                className="form-border"
                action="php/mail.php"
                method="post"
              >
                <div className="row g-4">
                  <div className="col-12">
                    <label className="form-label" htmlFor="name">
                      What is Your Name:
                    </label>
                    <input
                      id="name"
                      name="name"
                      type="text"
                      className="form-control py-1"
                      required=""
                    />
                  </div>
                  <div className="col-12">
                    <label className="form-label" htmlFor="email">
                      Your Email Address:
                    </label>
                    <input
                      id="email"
                      name="email"
                      type="email"
                      className="form-control py-1"
                      required=""
                    />
                  </div>
                  <div className="col-12">
                    <label className="form-label" htmlFor="form-message">
                      How can I Help you?:
                    </label>
                    <textarea
                      id="form-message"
                      name="form-message"
                      className="form-control py-1"
                      rows="4"
                      required=""
                    ></textarea>
                  </div>
                  <div className="col-12 text-center text-lg-start">
                    <button
                      id="submit-btn"
                      className="btn btn-dark rounded-0"
                      type="button"
                    >
                      Send{" "}
                      <span className="ms-3">
                        <i className="fas fa-arrow-right"></i>
                      </span>
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default Form;
