import React, { Fragment } from "react";
import faq from "../../assets/images/faq.png";

const Faq = () => {
  return (
    <Fragment>
      <section id="faq" className="section bg-light">
        <div className="container">
          <div className="row gy-5">
            <div
              className="col-lg-6 order-1 order-lg-0 wow fadeInUp"
              style={{ visibility: "visible" }}
            >
              <p className="text-center text-lg-start mb-2">
                <span className="bg-primary text-dark px-2">FAQ</span>
              </p>
              <h2 className="text-10 fw-600 text-center text-lg-start mb-5">
                Have any questions?
              </h2>

              <div className="accordion accordion-flush" id="faqTopics">
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingOne">
                    <button
                      className="accordion-button"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseOne"
                      aria-expanded="true"
                      aria-controls="flush-collapseOne"
                    >
                      What blockchain projects have you developed?
                    </button>
                  </h2>
                  <div
                    id="flush-collapseOne"
                    className="accordion-collapse collapse show"
                    aria-labelledby="flush-headingOne"
                    data-bs-parent="#faqTopics"
                  >
                    <div className="accordion-body">
                      Diverse projects, including decentralized applications
                      (DApps), centralized applications, and smart contracts.
                    </div>
                  </div>
                </div>
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingTwo">
                    <button
                      className="accordion-button collapsed"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseTwo"
                      aria-expanded="false"
                      aria-controls="flush-collapseTwo"
                    >
                      What blockchain platforms do you specialize in?
                    </button>
                  </h2>
                  <div
                    id="flush-collapseTwo"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingTwo"
                    data-bs-parent="#faqTopics"
                  >
                    <div className="accordion-body">
                      <p>
                        Proficient in Ethereum, Binance Smart Chain, and other
                        leading blockchain platforms.
                      </p>
                    </div>
                  </div>
                </div>
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingThree">
                    <button
                      className="accordion-button collapsed"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseThree"
                      aria-expanded="false"
                      aria-controls="flush-collapseThree"
                    >
                      Have you implemented tokenomics in your projects?
                    </button>
                  </h2>
                  <div
                    id="flush-collapseThree"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingThree"
                    data-bs-parent="#faqTopics"
                  >
                    <div className="accordion-body">
                      Yes, designed and integrated token systems for various
                      blockchain applications.
                    </div>
                  </div>
                </div>
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingFour">
                    <button
                      className="accordion-button collapsed"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseFour"
                      aria-expanded="false"
                      aria-controls="flush-collapseThree"
                    >
                      Can you give an example of a successful process
                      improvement?
                    </button>
                  </h2>
                  <div
                    id="flush-collapseFour"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingFour"
                    data-bs-parent="#faqTopics"
                  >
                    <div className="accordion-body">
                      Streamlined workflows, reducing operational costs and
                      enhancing overall efficiency.
                    </div>
                  </div>
                </div>
                <div className="accordion-item">
                  <h2 className="accordion-header" id="flush-headingFive">
                    <button
                      className="accordion-button collapsed"
                      type="button"
                      data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseFive"
                      aria-expanded="false"
                      aria-controls="flush-collapseThree"
                    >
                      Can you share a successful digital marketing case study?
                    </button>
                  </h2>
                  <div
                    id="flush-collapseFive"
                    className="accordion-collapse collapse"
                    aria-labelledby="flush-headingFive"
                    data-bs-parent="#faqTopics"
                  >
                    <div className="accordion-body">
                      Certainly, highlights include substantial traffic growth
                      and increased conversion rates.
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              className="col-lg-6 d-flex align-items-center justify-content-center order-0 order-lg-1 wow fadeIn"
              data-wow-delay="1s"
              style={{ visibility: "visible", animationDelay: "1s" }}
            >
              {" "}
              <img className="img-fluid" src={faq} title="FAQ" alt="faq" />{" "}
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default Faq;
