import React, { Fragment } from "react";

const ContactUs = () => {
  return (
    <Fragment>
      <section>
        <div className="hero-wrap">
          <div className="hero-mask opacity-8 bg-dark"></div>

          <div className="hero-bg parallax hero-bg-style"></div>
          <div className="hero-content section">
            <div
              className="container text-center py-5 wow fadeInUp"
              style={{ visibility: "visible" }}
            >
              <h2 className="text-10 fw-600 text-white mb-5">
                Interested in working with me?
              </h2>
              <a
                href="#contact"
                className="btn btn-primary rounded-0 smooth-scroll wow animate__animated animate__rubberBand"
                data-wow-delay="1s"
                style={{ visibility: "visible", animationDelay: "1s" }}
              >
                Hire Me!
              </a>{" "}
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default ContactUs;
