import React, { Fragment, useEffect } from "react";
import Typed from "typed.js";
import profile from "../../assets/images/pravin.png";

const Banner = () => {
  useEffect(() => {
    const typed = new Typed(".typed", {
      strings: ["Developer", "Consultant", ""],
      typeSpeed: 80,
      backSpeed: 50,
      loop: true,
    });

    return () => {
      typed.destroy();
    };
  }, []);
  return (
    <Fragment>
      <section
        id="home"
        className="bg-primary d-flex fullscreen position-relative py-5"
      >
        <div className="container my-auto py-4">
          <div className="row rg-50 mt-5 mt-sm-0">
            <div
              className="col-lg-7 text-center text-lg-start align-self-center order-1 order-lg-0 wow fadeIn"
              style={{ visibility: "visible" }}
            >
              <h1 className="text-12 fw-300 mb-0 text-uppercase">
                Hi, I'm <span>Pravin</span>, your go-to  
              </h1>

              <h2 className="text-21 fw-600 text-uppercase mb-0 ms-n1">
                <span className="typed"></span>
              </h2>
              <p className="text-5">from Tamil Nadu, India.</p>
              <a
                href="#portfolio"
                className="btn btn-dark rounded-0 smooth-scroll mt-3"
              >
                View My Works
              </a>
              <a
                href="#contact"
                className="btn btn-link text-dark smooth-scroll mt-3"
              >
                Contact Me
                <span className="text-4 ms-2">
                  <i className="far fa-arrow-alt-circle-down"></i>
                </span>
              </a>
            </div>
            <div className="col-lg-5 text-center align-self-center mb-4 mb-lg-0 order-0 order-lg-1">
              <div
                className="bg-light rounded-pill d-inline-block p-3 shadow-lg wow animate__animated animate__zoomIn"
                style={{ visibility: "visible" }}
              >
                {" "}
                <img
                  className="img-fluid rounded-pill d-block"
                  src={profile}
                  title="I'm Pravin"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
        <a href="#about" className="scroll-down-arrow text-dark smooth-scroll">
          <span className="animated">
            <i className="fas fa-arrow-down"></i>
          </span>
        </a>{" "}
      </section>
    </Fragment>
  );
};

export default Banner;
